'use client';

import { useState } from "react";
import { useRouter } from "next/navigation";
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";

export function AiPlayground() {
    const [field, setField] = useState("");
    const [response, setResponse] = useState("");
    const router = useRouter();

    const handleGptSubmit = async () => {
        try {
            const response = await fetch("/api/gptNotes", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify({
                    messages: [
                        {
                            role: "user",
                            content: field
                        }
                    ]
                })
            });

            const data = await response.json();
            console.log("API Response:", data);

            const content = data.choices[0].message.content;
            setResponse(content);
            router.push(`/response?field=${encodeURIComponent(content)}`);
        } catch (error) {
            console.error("Error calling API:", error);
        }
    };

    const handleFileUpload = async (e: React.ChangeEvent<HTMLInputElement>) => {
        if (!e.target.files?.length) return;
        
        const file = e.target.files[0];
        const formData = new FormData();
        formData.append('file', file);

        try {
            const response = await fetch('/api/upload', {
                method: 'POST',
                body: formData
            });

            const data = await response.json();
            console.log('Upload response:', data);
        } catch (error) {
            console.error('Error uploading file:', error);
        }
    };

    return (
        <div className="flex flex-col items-center justify-center min-h-screen p-4">
            <Card className="w-[600px]">
                <CardHeader>
                    <CardTitle>AI Playground</CardTitle>
                    <CardDescription>
                        Explore GPT and File Processing capabilities
                    </CardDescription>
                </CardHeader>
                <CardContent>
                    <Tabs defaultValue="gpt" className="w-full">
                        <TabsList className="grid w-full grid-cols-2">
                            <TabsTrigger value="gpt">GPT Notes</TabsTrigger>
                            <TabsTrigger value="file">File Upload</TabsTrigger>
                        </TabsList>
                        
                        <TabsContent value="gpt">
                            <div className="space-y-4">
                                <div className="space-y-2">
                                    <Label>Enter your text</Label>
                                    <Input 
                                        placeholder="Type something..." 
                                        value={field}
                                        onChange={(e) => setField(e.target.value)}
                                    />
                                </div>
                                {response && (
                                    <div className="mt-4 p-4 bg-gray-100 rounded-md">
                                        <Label>Response:</Label>
                                        <p className="mt-2">{response}</p>
                                    </div>
                                )}
                                <Button onClick={handleGptSubmit}>
                                    Generate Response
                                </Button>
                            </div>
                        </TabsContent>
                        
                        <TabsContent value="file">
                            <div className="space-y-4">
                                <div className="space-y-2">
                                    <Label>Upload File</Label>
                                    <Input 
                                        type="file"
                                        onChange={handleFileUpload}
                                        className="cursor-pointer"
                                    />
                                </div>
                            </div>
                        </TabsContent>
                    </Tabs>
                </CardContent>
                <CardFooter className="flex justify-between">
                    <Button variant="outline">Clear</Button>
                    <Button variant="default">Save</Button>
                </CardFooter>
            </Card>
        </div>
    );
} 